.header {
  background-color: #3c4041;
  grid-area: header;
  position: fixed;
  width: 100%;
  max-height: 10%;

  box-shadow: 2px 0 5px #fff;
}
.header img {
  max-width: 8%;
  max-height: 10%;
}
.loginBlock {
  float: right;
  padding: 10px;
}
.loginBlock a {
  color: rgba(255, 255, 255, 0.75);
  border: 2px solid rgba(255, 255, 255, 0.75);
  font-size: 24px;
  text-decoration: none;
}
.loginBlock a:hover {
  color: rgba(255, 255, 255, 1);
  border: 2px solid rgba(255, 255, 255, 1);
  font-size: 26px;
}
